import Vue from 'vue';
import { Card, Form, FormItem, Input, DatePicker, Tooltip, InputNumber, Radio, RadioGroup, Select, Option, Button } from 'element-ui';
import PageHeaderLayout from 'layouts/PageHeaderLayout/index.vue';
import AntIcon from 'components/AntIcon';
Vue.use(Card);
Vue.use(Form);
Vue.use(FormItem);
Vue.use(Input);
Vue.use(DatePicker);
Vue.use(Tooltip);
Vue.use(InputNumber);
Vue.use(Radio);
Vue.use(RadioGroup);
Vue.use(Select);
Vue.use(Option);
Vue.use(Button);
Vue.use(AntIcon);
export default Vue.extend({
    components: {
        PageHeaderLayout
    },
    data() {
        return {
            form: {
                title: '',
                date: '',
                goal: '',
                standard: '',
                client: '',
                invites: '',
                weight: '',
                public: '1',
                publicUsers: ''
            },
            rules: {
                title: [{ required: true, message: '请输入标题' }],
                date: [{ required: true, message: '请选择起止日期' }],
                goal: [{ required: true, message: '请输入目标描述' }],
                standard: [{ required: true, message: '请输入衡量标准' }]
            },
            submitting: false
        };
    },
    methods: {
        handleSubmit(e) {
            e.preventDefault();
            const formRef = this.$refs.form;
            formRef.validate((valid) => {
                if (valid) {
                    this.submitting = true;
                    this.$store
                        .dispatch('form/submitRegularForm', this.form)
                        .then(() => {
                        this.submitting = false;
                    })
                        .catch(() => {
                        this.submitting = false;
                    });
                }
            });
        }
    }
});



